<template>
  <div class="page-personal">
    <div>
      <div class="panel">
        <h3 class="tac">
          <span v-if="!english">个人信息</span><span v-else>Personal information</span>
        </h3>

        <ul class="page-personal-infos">
          <li class="page-personal-info"><span>昵&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;称：</span><span>{{ user.nickName }}</span></li>
          <li class="page-personal-info"><span>手机号码：</span><span>{{ user.userName }}</span></li>
          <li class="page-personal-info"><span>钱包地址：</span><span>{{ userWalletaddress }}</span></li>
          <li class="page-personal-info"><span>账户余额：</span><span>{{ user.balance }}</span></li>
        </ul>
      </div>

      <div class="panel">
        <van-card
          centered>

          <div slot="thumb">
            <div v-if="imgContent || imgContent !== ''">
              <img :src="imgContent" alt="" class="showImg">
            </div>
            <div v-else>
              <div v-if="uploadedImgC !== ''">
                <img :src="uploadedImgC" alt="" class="showImg">
              </div>
              <div v-else-if="userAvatar !== ''">
                <img :src="userAvatar" alt="" class="showImg">
              </div>
              <div v-else>
                <img src="../assets/img/avatar.png" alt="" class="showImg">
              </div>
            </div>
          </div>

          <div slot="tags">
            <div class="clearfix">
              <span class="fl">头像</span>
              <van-uploader class="fr" :after-read="onRead">
                选择图片
                <van-icon name="photograph" />
              </van-uploader>
            </div>
          </div>

          <div slot="footer" v-if="imgContent || imgContent !== ''">
            <van-button size="mini" @click.native="cancelUploadFn">取消</van-button>
            <van-button size="mini" @click.native="uploadFn">上传</van-button>
          </div>

        </van-card>
      </div>

    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Uploader, Icon, Card, Button, Toast } from 'vant'
import service from '@/api'
import base64Image from '../utils/base64Image'

export default {
  data () {
    return {
      result: '',
      imgContent: '',
      uploadedImgC: '',
      file: null
    }
  },
  computed: {
    ...mapState({
      english: states => states.english,
      user: states => states.user
    }),
    userAvatar () {
      return this.user.avatar ? this.user.avatar : ''
    },
    userWalletaddress () {
      return this.user.walletaddress ? this.user.walletaddress : '暂无'
    }
  },
  components: {
    VanUploader: Uploader,
    VanIcon: Icon,
    VanCard: Card,
    VanButton: Button
  },
  methods: {
    onRead (file) {
      this.file = file
      if (file.content) {
        this.imgContent = file.content
      }
    },
    uploadFn () {
      let _this = this
      if (this.file.file.size < 200 * 1024) {
        service.setAvatarupload({
          avatar: _this.imgContent
        })
        .then((data) => {
          if (data.success) {
            _this.uploadedImgC = _this.imgContent
            _this.imgContent = ''
            _this.user.avatar = _this.uploadedImgC
            _this.$store.dispatch('setChangeUser', _this.user)
          }
        })
        .catch(e => {
          _this.imgContent = ''
          Toast({
            duration: 2000,
            message: '上传失败，请重新选择！'
          })
        })
      }
      else {
        var img = new Image()
        img.src = this.file.content
        var compressImg = base64Image(img)
        service.setAvatarupload({
          avatar: compressImg
        })
        .then((data) => {
          if (data.success) {
            _this.uploadedImgC = compressImg
            _this.imgContent = ''
            _this.user.avatar = compressImg
            _this.$store.dispatch('setChangeUser', _this.user)
          }
        })
        .catch(e => {
          _this.imgContent = ''
          Toast({
            duration: 2000,
            message: '上传失败，请重新选择！'
          })
        })
      }
    },
    cancelUploadFn () {
      this.imgContent = ''
    }
  }
}
</script>

<style lang="scss" scoped>
.page-personal {
  padding: 20px;

  .page-personal-info {
    position: relative;
    font-size: 12px;
    padding: 0 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
    &:after {
      content: '';
      display: block;
      clear: both;
    }
    span:first-child {
      display: inline-block;
      width: 120px;
      text-align: center;
      float: left;
    }
    span:last-child {
      float: left;
      width: calc(100% - 124px);
    }
  }
  .showImg {
    width: 80px;
    height: 80px;
  }
}
</style>




