<template>
  <div class="page-help">
    <div class="panel">
      <h3 class="tac">
        <span v-if="!english">帮助中心</span><span v-else>Help Center</span>
      </h3>
      <div claas="" v-if="JSON.stringify(agreement) !== ''">
        <div v-html="agreement"></div>
      </div>
      <div claas="" v-else>
        <h2>暂无相关协议</h2>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  props: {
    agreement: {
      type: String,
      default: '',
      required: true
    }
  },
  data () {
    return {
    }
  },
  mounted () {
    this.getHelp()
  },
  methods: {
    getHelp () {
      this.$store.dispatch('getAgreement', {
        params: {
          // 抽奖的协议，需要跟后台商量传什么参数
          type: 'help'
        }
      })
    }
  },
  computed: {
    ...mapState({
      english: states => states.english
    })
  }
}
</script>

<style lang="scss" scoped>
.page-help {
  padding: 20px;
}
</style>




