<template>
  <div>
    <div class="page-recharge">
      <div class="panel">
        <h3 class="tac">
          <span v-if="!english">充值中心</span><span v-else>Recharge</span>
        </h3>
        <div class="pr-walletaddress">
          钱包地址：
          <div class="van-ellipsis">{{ userWalletaddress }}</div>
        </div>
        <div slot="footer" class="tar">
          <div v-if="user.walletaddress === ''">
            <van-button size="small" @click.native="bindWalletAddressBtn">绑定钱包地址</van-button>
          </div>
          <div v-else>
            <van-button size="small" @click.native="bindWalletAddressBtn">更换钱包地址</van-button>
          </div>
          <div v-if="showBindWtBlock">
            <div class="tal tips">请务必输入正确的 EOS 地址并核对，否则充值的数字资产丢失且不可找回！！！</div>
            <van-cell-group>
              <van-field 
                v-model="wtaddress"
                label="钱包地址"
                type="textarea"
                icon="clear"
                placeholder="请输入地址"
                @click-icon="wtaddress = ''"
                rows="1"
                autosize
              />
              <van-field
                center
                v-model="phoneText"
                label="短信验证码"
                placeholder="请输入"
                icon="clear"
                @click-icon="sendPhoneCode">
                <van-button  slot="icon" size="small" type="primary">发送验证码</van-button>
              </van-field>
              <div>
              <van-button size="small" @click.native="cancelBindWt">取消</van-button>
              <van-button size="small" type="primary" @click.native="DeterminBindWt">确定</van-button>
              </div>
            </van-cell-group>
          </div>
        </div>
      </div>
      <div class="panel">
        <div><span v-if="!english">请向下面二维码充值，充值成功后在“个人中心”查看账户余额！</span><span v-else>Recharge your two-dimensional code below</span></div>
        <br>
        <div class="tac">
          <img class="rechargeImg" :src="base64" alt="">
          <br>
          <div class="recharge-copy">
            <input 
              id="rechargeTarget" 
              class="recharge-copy-text" 
              type="text" 
              readonly 
              :value="link">
              <button class="recharge-copy-btn" data-clipboard-target="#rechargeTarget">
              <img src="../assets/img/copy.svg" alt="Copy to clipboard">
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Field, Button, CellGroup, Toast } from 'vant'
import Clipboard from 'clipboard'
import service from '@/api'
export default {
  props: {
    currentIndex: {
      type: Number
    }
  },
  data () {
    return {
      wtaddress: '',
      phoneText: '', // 短信验证码
      base64: '',
      link: '',
      showBindWtBlock: false,
      time60s: 0, // 发送手机短信的间隔
      timer: null
    }
  },
  mounted () {
    let _this = this
    this.timer = setInterval(function () {
      if (_this.time60s > 0) {
        _this.time60s = _this.time60s - 1
      }
    }, 1000)
    let copyBtn = document.getElementsByClassName('recharge-copy-btn')
    let clipboard = new Clipboard(copyBtn)
    clipboard.on('success', function (e) {
      Toast({
        duration: 2000,
        message: 'Copied!'
      })
    })
    clipboard.on('error', function (e) {
      Toast({
        duration: 2000,
        message: 'Error!'
      })
    })
  },
  watch: {
    currentIndex: function () {
      if (this.currentIndex === 4) {
        this.getRecharge()
      }
    }
  },
  computed: {
    ...mapState({
      english: states => states.english,
      user: states => states.user
    }),
    userWalletaddress () {
      return this.user.walletaddress ? this.user.walletaddress : '暂无'
    }
  },
  methods: {
    getRecharge () {
      let _this = this
      service.getRecharge().then(data => {
        if (data) {
          _this.base64 = data.qrcode
          _this.link = data.wcode
        }
      }).catch(e => {
        Toast({
          duration: 2000,
          message: e.message
        })
      })
    },
    bindWalletAddressBtn () {
      // bind wallet address
      if (!this.showBindWtBlock) {
        this.showBindWtBlock = true
      }
    },
    sendPhoneCode () {
      let _this = this
      if (_this.wtaddress === '') {
        Toast({
          duration: 2000,
          message: '请先输入钱包地址'
        })
        return
      }
      if (this.time60s > 0) {
        Toast({
          duration: 2000,
          message: '请先' + _this.time60s + '秒再试'
        })
        return
      }
      else {
        service.getSendSms().then((data) => {
          console.log(data)
          clearInterval(_this.timer)
          _this.timer = null
        })
      }
    },
    cancelBindWt () {
      this.phoneText = ''
      this.showBindWtBlock = false
    },
    DeterminBindWt () {
      let _this = this
      if (_this.wtaddress === '') {
        Toast({
          duration: 2000,
          message: '请输入钱包地址'
        })
        return
      }
      service.getWallet({
        wallet: _this.wtaddress,
        verifyCode: _this.phoneText
      }).then((data) => {
        Toast({
          duration: 2000,
          message: data.message
        })
        _this.user.wallet = _this.wtaddress
        _this.$store.dispatch('setChangeUser', _this.user)
        _this.phoneText = ''
        _this.showBindWtBlock = false
      }).catch((err) => {
        Toast({
          duration: 2000,
          message: err.message
        })
      })
    }
  },
  components: {
    VanField: Field,
    VanButton: Button,
    VanCellGroup: CellGroup
  }
}
</script>

<style lang="scss" scoped>
.page-recharge {
  padding: 20px;
}
.pr-walletaddress {
  padding: 10px;
  line-height: 2;
}
.rechargeImg {
  display: inline-block;
  border: 1px solid #ccc;
  width: 200px;
  height: 200px;
}
.tips {
  color: #f60;
}
.recharge-copy {
  position: relative;
  border: 1px solid #ccc;
  display: inline-block;
  width: 80%;
  padding: 0 5px;
  .recharge-copy-text {
    border: none;
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding-right: 30px;
  }
  .recharge-copy-btn {
    position: absolute;
    top: 0;
    right: 0;
    display: inline-block;
    width: 30px;
    height: 100%;
    padding: 0 5px;
    background: rgba(9, 9, 9, 0.1);
    border: none;
    cursor: pointer;
    img {
      position: relative;
      height: 100%;
    }
  }
}
</style>




