<script>
import { mapState } from 'vuex'
import footerbar from '../components/footerbar'
import homecontent from '../components/homecontent'
import marketcontent from '../components/marketcontent'
import mountaincontent from '../components/mountaincontent'
import minecontent from '../components/minecontent'

export default {
  name: 'Home',
  data () {
    return {
      selected: 'home', // 默认选中
      bgimg: 'none' // 不同页面的背景图
    }
  },
  mounted () {
    let _this = this
    setInterval(() => {
      _this.$store.dispatch('setCountdown', _this.countdown + 1)
      if (_this.countdown === 10) {
        _this.$store.dispatch('setToken', {
          cb: function (err, data) { // eslint-disable-line
            if (data) {
              window.sessionStorage.setItem('token', data)
              let obj = _this.user
              obj.token = data
              _this.$store.dispatch('setUser', obj)
            }
          }
        })
      }
    }, 600 * 1000)
  },
  computed: {
    ...mapState({
      english: states => states.english,
      user: states => states.user,
      countdown: states => states.countdown
    })
  },
  methods: {
    getSelected (val) {
      this.selected = val
      switch (val) {
        case 'home':
          this.bgimg = 'none'
          break
        case 'market':
          this.bgimg = 'none'
          break
        case 'mountain':
          this.bgimg = 'url(' + require('../assets/img/mountain.png') + ')'
          break
        case 'mine':
          this.bgimg = 'none'
          break
        default:
          this.bgimg = 'none'
      }
    }
  },
  components: {
    footerbar,
    homecontent,
    marketcontent,
    mountaincontent,
    minecontent
  }
}
</script>

<template>
  <div class="home">
    <div 
      class="home-wrap"
      :style="{backgroundImage: bgimg}">
      <mt-tab-container class="page-tabbar-container" v-model="selected">
        <mt-tab-container-item id="home">
          <homecontent :selected="selected"></homecontent>
        </mt-tab-container-item>
        <mt-tab-container-item id="market">
          <marketcontent :selected="selected"></marketcontent>
        </mt-tab-container-item>
        <mt-tab-container-item id="mountain">
          <mountaincontent :selected="selected"></mountaincontent>
        </mt-tab-container-item>
        <mt-tab-container-item id="mine">
          <minecontent :selected="selected"></minecontent>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>

    <footerbar @getSelected="getSelected"></footerbar>
  </div>
</template>

<style lang="scss" scoped>
.home {
  height: 100vh;
  overflow: hidden;
}
.home-wrap {
  height: 100%;
  overflow: auto;
  padding-bottom: 60px;
  background-size: cover;
  background-repeat: no-repeat;
  .page-tabbar-container {
    height: 100%;
    #home,
    #market,
    #mountain,
    #mine {
      height: 100%;
    }
  }
}
</style>


