<template>
  <div>
    <div class="head-title">
      <h1 class="marketcontent-title clearfix">
        <span class="fl">
          <h1 v-if="!english">集市</h1>
          <h1 v-else>Market</h1>
        </span>
      </h1>
    </div>
    <search-bar @sort="sortFn"></search-bar>
    <div
      v-infiniteScroll="loadmore"
      infinite-scroll-disabled="load"
      infinite-scroll-immediate-check="check"
      infinite-scroll-distance="10">
      <pet-list 
        :data="pets" 
        :title="petsTitle">
      </pet-list>
    </div>
    <div class="loading" v-if="loading">
      <van-loading type="spinner" color="black"></van-loading>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { InfiniteScroll } from 'mint-ui'
import { Loading } from 'vant'
import PetList from '../components/petList'
import SearchBar from '../components/searchBar'
export default {
  props: {
    selected: {
      type: String
    }
  },
  data () {
    return {
      searchVal: '',
      petsTitle: '市场的熊猫',
      pets: [],
      total: 0,
      page: 0,
      perpage: 30,
      order: 'asc',
      sort: 'years',
      loading: false,
      load: false,
      check: true
    }
  },
  watch: {
    selected () {
      if (this.selected === 'market') {
        if (this.english) {
          this.petsTitle = 'Market Pets'
        }
        else {
          this.petsTitle = '集市的熊猫'
        }
        this.page = 0
        this.pets = []
        this.getPets(this.order, this.sort)
      }
    }
  },
  computed: {
    ...mapState({
      english: states => states.english
    })
  },
  mounted () {
  },
  methods: {
    getPets (vorder, vsort) {
      let _this = this
      this.loading = true
      this.$store.dispatch('getPets', {
        params: {
          page: _this.page,
          perpage: _this.perpage,
          order: vorder,
          sort: vsort
        },
        cb: function (err, data) { // eslint-disable-line
          if (data) {
            if (data.info) {
              _this.pets = _this.pets.concat(data.info)
            }
            _this.total = data.total
          }
          // 这里再限制下，如果大于总的，load 设为true，不再触发无限滚动
          if ((_this.page + 1) * _this.perpage >= _this.total) {
            _this.load = true
          }
          _this.loading = false
        }
      })
    },
    loadmore () {
      setTimeout(() => {
        if ((this.page + 1) * this.perpage < this.total) {
          this.page += 1
          this.getPets(this.order, this.sort)
          this.load = false
        }
      }, 2000)
    },
    sortFn (order, sort) {
      this.pets = []
      this.getPets(order, sort)
    }
  },
  components: {
    PetList,
    InfiniteScroll,
    SearchBar,
    VanLoading: Loading
  }
}
</script>

<style lang="scss" scoped>
.marketcontent-title {
  padding: 0 8px 0 20px;
}
</style>



