<template>
  <div>
    <div class="page-invite">

      <div>
        <h1 class="page-invite-title">
          <span v-if="!english"><span>限时活动：</span>邀请好友</span>
          <span v-else>Invite Friends</span>
        </h1>
      </div>

      <div>
        <span v-if="!english">您已经成功邀请 <span class="page-color-red">{{ hasM }}</span> 位好友，还差 <span class="page-color-red">{{ needN }}</span> 位</span>
        <span v-else>您已经成功邀请<span class="page-color-red">{{ hasM }}</span> 位好友，还差<span class="page-color-red">{{ needN }}</span> 位</span>
      </div>

      <div>

        <div v-if="!english">
          <div>
            复制以下二维码、链接发送给好友，邀请<span class="page-color-red"> {{invite.total}} </span>个好友并注册成功，你会得到宠物熊猫一只
          </div>
          <div class="code tac">
            <img :src="imgBase64" alt="">
            <br>
            <div class="invite-copy">
              <input 
                id="target" 
                class="invite-copy-text" 
                type="text" 
                readonly 
                :value="link">
                <button class="invite-copy-btn" data-clipboard-target="#target">
                <img src="../assets/img/copy.svg" alt="Copy to clipboard">
              </button>
            </div>
          </div>
          <div class="link">
            太上熊猫是基于区块链的宠物养成小游戏，她具有唯一性，不可篡改，并具有收藏性价值。注册并邀请好友送太上熊猫哦！
          </div>
        </div>

        <div v-else>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Toast } from 'vant'
import Clipboard from 'clipboard'
export default {
  data () {
    return {
    }
  },
  mounted () {
    let copyBtn = document.getElementsByClassName('invite-copy-btn')
    let clipboard = new Clipboard(copyBtn)
    clipboard.on('success', function (e) {
      Toast({
        duration: 2000,
        message: 'Copied!'
      })
    })
    clipboard.on('error', function (e) {
      Toast({
        duration: 2000,
        message: 'Error!'
      })
    })
  },
  computed: {
    ...mapState({
      english: states => states.english,
      invite: states => states.api.invite
    }),
    link () {
      return this.invite && this.invite.invitation_url ? this.invite.invitation_url : '../assets/img/invite.png'
    },
    imgBase64 () {
      return this.invite && this.invite.image ? this.invite.image : ''
    },
    hasM () {
      return this.invite && this.invite.surplus ? (this.invite.total - this.invite.surplus) : 0
    },
    needN () {
      return this.invite && this.invite.surplus ? this.invite.surplus : this.invite.total
    },
    total () {
      return this.invite && this.invite.total ? this.invite.total : 10
    }
  }
}
</script>

<style lang="scss" scoped>
.page-invite {
  margin-top: 20px;
  padding: 0 20px;
  font-size: 14px;
  text-align: center;
  line-height: 2;
  @media screen and (min-width: 768px) {
    .page-invite-title {
      font-size: 24px;
    }
  }
}
.invite-copy {
  position: relative;
  border: 1px solid #ccc;
  display: inline-block;
  width: 80%;
  padding: 0 5px;
  .invite-copy-text {
    border: none;
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding-right: 30px;
  }
  .invite-copy-btn {
    position: absolute;
    top: 0;
    right: 0;
    display: inline-block;
    width: 30px;
    height: 100%;
    padding: 0 5px;
    background: rgba(9, 9, 9, 0.1);
    border: none;
    cursor: pointer;
    img {
      position: relative;
      height: 100%;
    }
  }
}
</style>




