<template>
  <div class="page-petinfo">
    <div class="panel page-petinfo-box">
      <h3 class="tac">
        <span v-if="!english">宠物信息</span>
        <span v-else>Pets details</span>
      </h3>
      <van-row>
        <van-col span="8">宠物编号</van-col>
        <van-col span="8">宠物稀有属性值</van-col>
        <van-col span="8">宠物年龄</van-col>
      </van-row>
      <van-list
        class="pagePetInfo-con"
        v-model="loading"
        :finished="finished"
        @load="onLoad"
        >
        <div v-for="(item, index) in myPet" :key="index">
          <van-row>
            <van-col span="8">{{ item.number }}</van-col>
            <van-col span="8"><span v-if="item.rate">有</span><span v-else>无</span></van-col>
            <van-col span="8">{{ item.years }}</van-col>
          </van-row>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { List, Row, Col } from 'vant'

export default {
  data () {
    return {
      loading: false,
      finished: false,
      myPet: [],
      total: 0,
      page: 0,
      perpage: 30,
      order: 'asc',
      sort: 'years'
    }
  },
  mounted () {
    this.myPet = []
    this.getPets()
  },
  computed: {
    ...mapState({
      english: states => states.english,
      user: states => states.user
    })
  },
  methods: {
    getPets (vorder, vsort) {
      let _this = this
      this.loading = true
      this.$store.dispatch('getPets', {
        params: {
          page: _this.page,
          perpage: _this.perpage,
          order: vorder ? vorder : _this.order,
          sort: vsort ? vsort : _this.sort,
          memberId: _this.user.memberId
        },
        cb: function (err, data) { // eslint-disable-line
          // console.log(err, data)
          if (data) {
            if (data.info) {
              _this.myPet = _this.myPet.concat(data.info)
            }
            _this.total = data.total
          }
          // 这里再限制下，如果大于总的，load 设为true，不再触发无限滚动
          if ((_this.page + 1) * _this.perpage >= _this.total) {
            _this.finished = true
          }
          _this.loading = false
        }
      })
    },
    onLoad () {
      let _this = this
      setTimeout(() => {
        _this.page = _this.page + 1
        _this.getPets()

        _this.loading = false

        if (_this.myPet.length >= _this.total) {
          _this.finished = true
        }
      }, 500)
    }
  },
  components: {
    VanList: List,
    VanRow: Row,
    VanCol: Col
  }
}
</script>

<style lang="scss" scoped>
.page-petinfo {
  padding: 20px;
  height: 100%;
}
.page-petinfo-box {
  position: relative;
  height: 80%;
}
.pagePetInfo-con {
  height: calc(100% - 40px);
  overflow: auto;
}
</style>




