<template>
  <div>
    <div class="homehead-title clearfix head-title">
      <span class="fl">
        <h1 v-if="!english">首页</h1>
        <h1 v-else>Home</h1></span>
      <div class="fr head-right-icon">
        <mt-button 
        type="default"
        @click.native="lotteryFn"
        class="homehead-lottery">
          <span v-if="!english">抽奖</span>
          <span v-else>Lottery</span>
        </mt-button>
      </div>
    </div>
    <router-view></router-view>
    <div class="homehead-announcement panel">
      <span class="an-box">
        <span class="an-title">
          <span v-if="!english">公告</span>
          <span v-else>Announcement</span>
        </span>
        <i class="iconfont icon-laba an-laba"></i>
        <notice-bar
          :text="notice"
          class="an-text"
          background="#fff">
        </notice-bar>
      </span>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { NoticeBar } from 'vant'
import Agreement from '@/components/agreement'
import Swiper from '@/components/swiper'
import service from '@/api'
export default {
  name: 'homehead',
  // i18n: {
  //   'zh-CN': {
  //     home: '首页',
  //     lottery: '抽奖',
  //     announcement: '公告'
  //   },
  //   'en-US': {
  //     home: 'Home',
  //     lottery: 'Lottery',
  //     announcement: 'Announcement'
  //   }
  // },
  data () {
    return {
      notice: ''
    }
  },
  created () {
  },
  mounted () {
    this.getAgreement()
    this.getNotice()
  },
  computed: {
    ...mapState({
      balance: states => parseFloat(states.user.balance),
      english: states => states.english,
      agreement: states => states.agreement
    })
  },
  methods: {
    // 获取协议内容
    getAgreement () {
      this.$store.dispatch('getAgreement', {
        cb: function (data) {
          // console.log(data)
        }
      })
    },
    // 获取公告内容
    getNotice () {
      let _this = this
      service.getNotice().then(data => {
        if (data) {
          _this.notice = data.text
        }
      }).catch(e => {
        _this.notice = '暂无公告'
      })
    },
    // 点击抽奖
    lotteryFn () {
      this.$router.push({name: 'Lottery'})
    }
  },
  components: {
    Agreement,
    Swiper,
    NoticeBar
  }
}
</script>

<style lang="scss">
  .lottery-popup {
    width: 100%;
    height: 100%;
    background-color: #fff;
  }
  .homehead-title {
    padding: 0 8px 0 20px;
    .homehead-lottery {
      line-height: 1;
    }
  }
  .homehead-announcement {
    padding: 8px 8px 8px 20px;
    .an-box {
      display: inline-block;
      width: 100%;
    }
    .an-title,
    .an-laba,
    .an-text {
      display: inline-block;
      vertical-align: middle;
    }
    .an-title {
      width: 10%;
    }
    .an-laba {
      width: 6%;
      text-align: right;
    }
    .an-text {
      width: 80%;
    }
    .announcement-text-block {
      position: relative;
      display: inline-block;
      overflow: hidden;
      .announcement-text {
        position: relative;
        white-space: nowrap;
      }
    }
  }
  .homehead-laba {
  
  }
</style>



