<template>
  <div class="pets panel">

    <div class="pets-title"><h3>{{ title }}</h3></div>

    <div>
      <section v-if="data.length" class="petItems">
        <div
          class="petItem col-md-3"
          v-for="(item, index) in data"
          :key="index"
          @click="toPet(item)">
            <div class="petItem-img" v-if="item">
              <img class="pet-img" v-lazy.container="item.imgurl" alt="">
              <span class="pet-number">{{ item.number }}</span>
            </div>
            <div class="price" v-if="item">{{ item.price }}</div>
        </div>
      </section>
      <section v-else class="tac">
        <div>
          <img class="empty" src="../assets/img/WechatIMG11.png" alt="">
        </div>
        <br>
        <div>
          您还没有熊猫哦
        </div>
        <br>
      </section>
    </div>

  </div>
</template>

<script>
import { Lazyload } from 'mint-ui'
export default {
  props: {
    data: {
      type: Array
    },
    title: {
      type: String,
      default: ''
    }
  },
  methods: {
    toPet: function (pet) {
      if (this.title !== '我的熊猫') {
        pet.isMarket = true
      }
      else {
        pet.isMarket = false
      }
      console.log('-----')
      console.log(pet)
      this.$store.commit('PET', pet)
      this.$router.push({name: 'Pet'})
    }
  },
  components: {
    Lazyload
  }
}
</script>

<style lang="scss" scoped>
.pets {
  margin: 8px;
  padding: 12px;
  background-color: #fff;
  .pets-title {
    margin: 10px;
  }
}
.petItems {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  min-height: 70%;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-flow: row wrap;
  flex-flow: row wrap;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}
.petItem {
  display: inline-block;
  vertical-align: middle;
  width: 33.33%;
  overflow: hidden;
  text-overflow: ellipsis;
  .petItem-img {
    position: relative;
    .pet-img {
      width: 100%;
      
      border: 1px solid rgba(#ccc, 0.5);
      // border-radius: 5px;
    }
    .pet-number {
      position: absolute;
      color: #9b9b9b;
      background-color: hsla(0,0%,100%,0.8);
      left: 0px;
      font-size: 10px;
      top: 6px;
      padding: 3px 5px 3px 0;
      border-radius: 0 10px 10px 0;
    }
  }
}
@media screen and (min-width: 960px) {
  .col-md-3 {
    width: 25%;
  }
}
@media screen and (max-width: 959px) {
  .col-md-3 {
    width: 33.33%;
  }
}

image[lazy=loading] {
  width: 40px;
  height: 300px;
  margin: auto;
}

.empty {
  width: 60%;
}
</style>



