<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      selected: 'home' // 默认选中值
    }
  },
  mounted () {
    let pageName = this.$router.history.current.name
    switch (pageName) {
      case 'Home':
        this.selected = 'home'
        break
      case 'Market':
        this.selected = 'market'
        break
      case 'Mountain':
        this.selected = 'mountain'
        break
      case 'Mine':
        this.selected = 'mine'
        break
      default:
        this.selected = 'home'
    }
  },
  computed: {
    ...mapState({
      english: states => states.english
    })
  },
  watch: {
    selected: function (newVal, oldVal) {
      switch (this.selected) {
        case 'home':
          // go to page home
          this.$router.push({meta: {auth: true}, path: '/home'})
          break
        case 'market':
          // go to page market
          this.$router.push({meta: {auth: true}, path: '/market'})
          break
        case 'mountain':
          // go to page mountain
          this.$router.push({meta: {auth: true}, path: '/mountain'})
          break
        case 'mine':
          // go to page mine
          this.$router.push({meta: {auth: true}, path: '/mine'})
          break
        default:
          // go to error
      }

      this.$emit('getSelected', this.selected)
    }
  }
}
</script>

<template>
  <mt-tabbar v-model="selected" :fixed="true">
    <mt-tab-item id="home" class="page-logo">
      <i class="page-img iconfont icon-panda" slot="icon"></i>
      <span v-if="!english">首页</span><span v-else>Home</span>
    </mt-tab-item>
    <mt-tab-item id="market" class="page-logo">
      <i class="page-img iconfont icon-tiaosaoshichang" slot="icon"></i>
      <span v-if="!english">集市</span><span v-else>Market</span>
    </mt-tab-item>
    <mt-tab-item id="mountain" class="page-logo">
      <i class="page-img iconfont icon-faxian" slot="icon"></i>
      <span v-if="!english">熊猫村</span><span v-else>page 2</span>
    </mt-tab-item>
    <mt-tab-item id="mine" class="page-logo">
      <i class="page-img iconfont icon-wode" slot="icon"></i>
      <span v-if="!english">我的</span><span v-else>Mine</span>
    </mt-tab-item>
  </mt-tabbar>
</template>

<style lang="scss" scoped>
</style>




