<template>
  <div class="panel">
    <div class="sb clearfix">

      <van-row>
        <van-col span="10">
          <search
            class="sb-search"
            v-model="value"
            placeholder="搜索"
            background="#fff"
            @search="onSearch"
          />
        </van-col>
        <van-col span="14">
          <div class="sb-order">
            <ul>
              <li 
                class="sb-orderItem iconfont" 
                :class="{'icon-xiangshang': isup && currentIndex === index, 'icon-xiangxia': !isup && currentIndex === index, 'active': currentIndex === index}"
                @click="orderFn(index)"
                v-for="(item, index) in orderArr" 
                :key="index">
                {{ item.name }}
              </li>
            </ul>
          </div>
        </van-col>
      </van-row>


    </div>
  </div>
</template>

<script>
import { Search, Row, Col } from 'vant'
export default {
  data () {
    return {
      value: '',
      orderArr: [{id: 'years', name: '代数', isup: true}, {id: 'birth', name: '出生日期', isup: true}],
      isup: true,
      currentIndex: 0,
      page: 0,
      perpage: 30,
      order: 'asc',
      sort: 'years'
    }
  },
  methods: {
    onSearch () {
      let _this = this
      this.$store.dispatch('getPets', {
        params: {
          page: _this.page,
          perpage: _this.perpage,
          order: _this.order,
          sort: _this.years,
          petId: '',
          memberId: '',
          status: 0
        },
        cb: function (err, data) {
          console.log(err, data)
          if (data) {
            _this.pets = _this.pets.concat(data.info)
            _this.total = data.total
          }
          if ((_this.page + 1) * _this.perpage >= _this.total) {
            _this.loading = true
          }
        }
      })
    },
    onCancel () {
      console.log(this.value)
    },
    orderFn (index) {
      this.isup = !this.isup
      this.currentIndex = index
      let order = this.isup ? 'asc' : 'desc'
      this.$emit('sort', order, this.orderArr[index].id)
    }
  },
  components: {
    Search,
    VanRow: Row,
    VanCol: Col
  }
}
</script>

<style lang="scss" scoped>
.sb {
  width: 100%;
  .sb-search,
  .sb-order {
    height: 40px;
    line-height: 40px;
  }
  .sb-search {
    padding: 4px 6px;
  }
  .sb-order {
    position: relative;
    text-align: right;
  }
  .sb-orderItem {
    display: inline-block;
    margin: 0 5px;
    font-size: 16px;
  }
  @media screen and (min-width: 768px) {
    .sb-orderItem {
      font-size: 16px;
      width: 90px;
    }
  }
}
</style>




