<template>
  <div>

    <div class="head-title">
      <h1 class="minecontent-title clearfix">

        <span class="fl">
          <h1 v-if="!english">个人中心</h1>
          <h1 v-else>Personal Center</h1>
        </span>

        <div class="fr head-right-icon">
          <mt-button 
            class="fr minecontent-lang" 
            @click.native="changeLanguage">
            <span 
              class="iconfont" 
              :class="{'icon-yingwenyuyan': english, 'icon-zhongwenyuyan': !english}">
            </span>
          </mt-button>
        </div>

      </h1>
    </div>

    <div class="minecontent-block panel">
      <mt-cell :title="user.nickName">
        <img slot="icon" class="avatar" :src="user.avatar" width="24" height="24">
        <span @click="openConfirm">
          <span v-if="!english"> 退出 &nbsp; > </span>
          <span v-else> Log out &nbsp; > </span>
        </span>
      </mt-cell>
    </div>

    <div class="minecontent-block panel">
      <div v-for="(item, index) in mineArr" :key="index">
        <mt-cell @click.native="openPop(item, index)">
          <span v-if="!english">{{ item.text }}</span><span v-else>{{ item.textEn }}</span>
          <span slot="icon" class="iconfont" :class="item.icon"></span>
        </mt-cell>
      </div>
    </div>

    <mt-popup 
      v-model="popShow" 
      popup-transition="popup-fade" 
      class="mine-detailInfo">

      <mt-cell 
        @click.native="closePop" 
        :title="!english ? '返回' : 'Back'">
      </mt-cell>

      <div class="mine-detailInfo-box" v-show="currentIndex === 0">
        <page-personal></page-personal>
      </div>

      <div class="mine-detailInfo-box" v-show="currentIndex === 1">
        <page-pet-info></page-pet-info>
      </div>

      <div class="mine-detailInfo-box" v-show="currentIndex === 2">
        <page-help :agreement="agreement"></page-help>
      </div>

      <div class="mine-detailInfo-box" v-show="currentIndex === 3">
        <page-tell></page-tell>
      </div>

      <div class="mine-detailInfo-box" v-show="currentIndex === 4">
        <page-recharge :currentIndex="currentIndex"></page-recharge>
      </div>

      <div class="mine-detailInfo-box" v-show="currentIndex === 5">
        <page-invite></page-invite>
      </div>

    </mt-popup>

  </div>
</template>

<script>
import { mapState } from 'vuex'
import { MessageBox } from 'mint-ui'
import PagePersonal from '@/components/pagePersonal'
import PagePetInfo from '@/components/pagePetInfo'
import PageHelp from '@/components/pageHelp'
import PageTell from '@/components/pageTell'
import PageRecharge from '@/components/pageRecharge'
import PageInvite from '@/components/pageInvite'
import service from '@/api'
export default {
  name: 'minecontent',
  i18n: {
    'zh-CN': {

    },
    'en-US': {

    }
  },
  props: {
    selected: {
      type: String
    }
  },
  data () {
    return {
      popupPersonal: false,
      popupPetInfo: false,
      popupHelp: false,
      popupTell: false,
      popupRecharge: false,
      popupInvite: false,
      currentIndex: -1,
      popShow: false,
      mineArr: [
        {
          text: '个人中心',
          textEn: 'Personal information',
          variable: 'popupPersonal',
          icon: 'icon-gerenxinxi'
        },
        {
          text: '宠物信息',
          textEn: 'Pet information',
          variable: 'popupPetInfo',
          icon: 'icon-chongwu'
        },
        {
          text: '帮助中心',
          textEn: 'Help Center',
          variable: 'popupHelp',
          icon: 'icon-bangzhuzhongxin'
        },
        {
          text: '客服邮箱',
          textEn: 'Customer service phone',
          variable: 'popupTell',
          icon: 'icon-youxiang'
        },
        {
          text: '点我充值',
          textEn: 'Recharge',
          variable: 'popupRecharge',
          icon: 'icon-chongzhi'
        },
        {
          text: '好友邀请',
          textEn: 'Invite Friends',
          variable: 'popupInvite',
          icon: 'icon-yaoqinghaoyou1'
        }
      ]
    }
  },
  mounted () {
    this.getInviteData()
    this.getBalance()
  },
  computed: {
    ...mapState({
      english: states => states.english,
      user: state => state.user,
      inviteData: state => state.invite,
      agreement: state => state.agreement
    })
  },
  watch: {
    currentIndex () {
      this.currentIndex === -1 ? this.popShow = false : this.popShow = true
    }
  },
  methods: {
    openConfirm () {
      MessageBox.confirm('确定退出账号吗?', '').then(action => {
        // 确定退出
        this.$router.push('/')
        this.$store.dispatch('setUser', {})
      }).catch(action => {
      })
    },
    // change language
    changeLanguage () {
      this.$store.commit('ENGLISH', !this.english)
      // this.english ? Locale.use('en-US', enUS) : Locale.use('zh-CN', zhCN)
    },
    // 打开弹出层
    openPop (item, index) {
      this.currentIndex = index
    },
    // 关闭弹出层
    closePop () {
      this.currentIndex = -1
    },
    // 获取邀请数据
    getInviteData () {
      this.$store.dispatch('getInvite', {
        cb: function (err, data) { // eslint-disable-line
          // console.log(err, data)
        }
      })
    },
    // 获取余额
    getBalance () {
      service.getBalance({
        memberId: this.user.memberId
      }).then((data) => {
        this.$store.dispatch('setBalance', data.balance)
      }).catch((err) => { // eslint-disable-line
      })
    },
    getAgreement () {
      this.$store.dispatch('getAgreement', {
        params: {
          // 抽奖的协议，需要跟后台商量传什么参数
          type: 'help'
        }
      })
    }
  },
  components: {
    PagePersonal,
    PagePetInfo,
    PageHelp,
    PageTell,
    PageRecharge,
    PageInvite
  }
}
</script>

<style lang="scss" scoped>
  .minecontent-title {
    padding: 0 8px 0 20px;
  }
  .minecontent-block {
    margin: 8px;
    .avatar {
      width: 100px;
      height: 100px;
      margin-right: 20px;
      border-radius: 50px;
      background-color: #ededed;
    }
  }
  .mine-detailInfo {
    width: 100%;
    height: 100%;
    background-color: #fff;
  }
  .mine-detailInfo-box {
    position: relative;
    height: 100%;
  }
</style>


