<template>
  <div class="agreement">
    <div v-if="JSON.stringify(agreement) !== '{}'">
      <div v-html="agreement"></div>
    </div>
    <div v-else>
      <h2>暂无相关协议</h2>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  props: {
    agreement: {
      type: String,
      default: '',
      required: true
    }
  },
  data () {
    return {
    }
  },
  computed: {
    ...mapState({
      english: states => states.english
    })
  },
  mounted () {

  },
  components: {
  }
}
</script>

<style lang="scss" scoped>
.agreement {
  padding: 50px 10px 10px;
  overflow: auto;
}

</style>



